            "text": "Define and tidy your room in one fell swoop with this distinctive dressing table. Part of our Dayton collection, it is designed for small spaces, but can work anywhere you need storage. Three drawers offer space for your daily clothes and an extra set of bedding. In addition to function, it has a great shape with a subtle chevron pattern and incredible tonality. The dresser has 3 large drawers and beautiful parquet accents on the face of the drawers. Made with solid mango wood, mango veneers, birch wood and light carbon finish. Kiln-dried wood helps prevent warping, splitting, cracking, and the development of mold. The drawers have metal guides that ensure smooth movement. Sturdy crescent safety stops securely hold drawers in place.",
